.card img {
  max-width: 100%;
  max-height: 100%;
}
.card {
  width: 200px;
  height: 200px;
  text-align: center;
  margin: auto;
  margin-bottom: 20px;
  background-color: transparent;
}

.card:hover {
  background-color: white;
  cursor: pointer;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}



