nav p {
  text-align: right;
  color: white;
}

.red {
  color: red;
  -webkit-animation: red-flash 0.8s;
          animation: red-flash 0.8s;
}

.green {
  color: rgb(0, 177, 0);
  -webkit-animation: green-flash 0.8s;
          animation: green-flash 0.8s;
}

@-webkit-keyframes red-flash {
    from {
        color: white;
    } to {
        color: red;
    }
}

@keyframes red-flash {
    from {
        color: white;
    } to {
        color: red;
    }
}

@-webkit-keyframes green-flash {
    from {
        color: white;
    } to {
        color: green;
    }
}

@keyframes green-flash {
    from {
        color: white;
    } to {
        color: green;
    }
}


